html, body {
  height: 100%;
  margin: 0;
  font-size: 16px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 400;
  line-height: 1.8em;
  color: #666;
} 

.pig1 , .pig2, .pig3{
  position: relative;
  opacity: 0.90;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.pig1{
  background-image: url('./img/pexels-photo-92866.jpeg');
  min-height: 1000px;
}

.pig2{
  background-image: url('./img/pexels-photo-373934.jpeg');
  min-height: 400px;
}

.pig3{
  background-image: url('./img/pexels-photo-773471.jpeg');
  min-height: 400px;
}
.section{
  text-align: center;
  padding: 50px 80px;
  min-height: 600px;
}

.section-light{
  background-color: #f4f4f4;
  color: #666;
}
.section-dark{
  background-color:#282e34 ;
  color: #ddd;
}
.pigtext{

  position: absolute ;
  top: 50%;
  width: 100%;
  text-align: center;
  color: #000;
  font-size: 27px;
  letter-spacing: 8px;
  text-transform: uppercase;
}
.pigtext .border{
    background-color: #111;
    color: #fff;
    padding: 20px;
}
.pigtext .border.trans{
  background-color: transparent;
  
}



